import 'assets/css/dashboard.css';
import ReactApexChart from 'react-apexcharts';
import chartData from 'app/data/dashboard/barChart';
import { NaverMap, RenderAfterNavermapsLoaded, Marker } from 'react-naver-maps';
import { top10User, top10Station, history } from 'app/data/dashboard/gridData';
import options from 'app/data/dashboard/doughnutChart';
import markerList from 'app/data/dashboard/mapData';
import { useEffect, useState } from 'react';
import chartOption2 from 'app/data/deviceManagement/deviceDetailHistoryGraph/chart';

function NaverMapAPI() {
  const navermaps = window.naver.maps;

  const [mapData, setMapData] = useState([]);

  useEffect(() => {
    setMapData(markerList.line);
    /* fetch(markerList)
      .then((res) => {
        console.log(res);
        setMyData(res.json())
      })
    .then((data) => {
      console.log(data);
      setMyData(data.line);
    }); */
  }, []);

  if (mapData.length === 0) return;

  return (
    <NaverMap defaultCenter={{ lat: 37.497175, lng: 127.027926 }} defaultZoom={14}>
      {mapData.map((input) => (
        <Marker
          key={input.station}
          position={new navermaps.LatLng(...input.code)}
          animation={2}
          title={input.station}
          icon={{
            content: `<div class="markerBox">
                <div class="title f__medium">${input.station}</div>
                  <div class="marker__wrap">
                    <img src='${process.env.PUBLIC_URL}/images/icon/marker/${input.status}.png' alt="${input.status}"/>
                  </div>
                </div>`,
          }}
        />
      ))}
    </NaverMap>
  );
}

function DashBoard() {
  const groupList = ['그룹1', '그룹2'];
  const stationList = ['강남역3번출구', '스테이션명2'];
  const [group, setGroup] = useState('');
  const [stationStatus, setStationStatus] = useState('');
  const [useStation, setUseStation] = useState('주간');
  const [user, setUser] = useState('주간');

  return (
    <div className="section__grid__wrap content dashboard">
      {/* <div className="page__title__wrap">
      {/* <!-- 전체 스테이션 --> */}
      <section className="station__status__box">
        <div className="section__inner">
          <div className="top">
            <p className="f__medium">전체</p>
            <button>
              <svg viewBox="0 0 24 24" stroke="currentColor">
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M12 5v.01M12 12v.01M12 19v.01M12 6a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2z"
                />
              </svg>
            </button>
          </div>
          <dl>
            <dt className="f__medium">25</dt>
            <dd className="f__medium">전체 스테이션</dd>
          </dl>
        </div>
      </section>
      {/* <!-- // 전체 스테이션 --> */}
      {/* <!-- 대여가능 스테이션--> */}
      <section className="station__status__box">
        <div className="section__inner">
          <div className="top">
            <p className="f__medium">대여가능</p>
            <button>
              <svg viewBox="0 0 24 24" stroke="currentColor">
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M12 5v.01M12 12v.01M12 19v.01M12 6a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2z"
                />
              </svg>
            </button>
          </div>
          <dl>
            <dt className="f__medium">4</dt>
            <dd className="f__medium">대여가능 스테이션</dd>
          </dl>
        </div>
      </section>
      {/* <!-- // 대여가능 스테이션--> */}
      {/* <!-- 대여불가 스테이션--> */}
      <section className="station__status__box">
        <div className="section__inner">
          <div className="top">
            <p className="f__medium">대여불가</p>
            <button>
              <svg viewBox="0 0 24 24" stroke="currentColor">
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M12 5v.01M12 12v.01M12 19v.01M12 6a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2z"
                />
              </svg>
            </button>
          </div>
          <dl>
            <dt className="f__medium">33</dt>
            <dd className="f__medium">대여불가 스테이션</dd>
          </dl>
        </div>
      </section>
      {/* <!-- // 대여불가 스테이션--> */}
      {/* <!-- 장애 스테이션--> */}
      <section className="station__status__box">
        <div className="section__inner">
          <div className="top">
            <p className="f__medium">장애</p>
            <button>
              <svg viewBox="0 0 24 24" stroke="currentColor">
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M12 5v.01M12 12v.01M12 19v.01M12 6a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2z"
                />
              </svg>
            </button>
          </div>
          <dl>
            <dt className="f__medium">42</dt>
            <dd className="f__medium">대여가능 (점검요망)</dd>
          </dl>
        </div>
      </section>
      {/* <!-- // 장애 스테이션--> */}
      {/* <!-- 충전소 현황 --> */}
      <section className="charging__station__status">
        <div className="section__inner">
          <div className="map__wrap">
            <div className="top">
              <p className="normal__title f__medium">충전소 현황</p>
              <div className="selector__wrap">
                <select className="custom__select" onChange={(e) => setGroup(e.target.value)}>
                  <option value="">그룹명</option>
                  {groupList.map((obj, idx) => (
                    <option key={obj} value={obj}>
                      {obj}
                    </option>
                  ))}
                </select>
                <select
                  className="custom__select"
                  onChange={(e) => setStationStatus(e.target.value)}
                >
                  <option value="">스테이션명</option>
                  {stationList.map((obj, idx) => (
                    <option key={obj} value={obj}>
                      {obj}
                    </option>
                  ))}
                </select>
              </div>
            </div>
            <div id="map" className="map">
              <RenderAfterNavermapsLoaded
                ncpClientId="qelkdx0v6n"
                error={<p>Maps Load Error</p>}
                loading={<p>Maps Loading...</p>}
              >
                <NaverMapAPI />
              </RenderAfterNavermapsLoaded>
            </div>
          </div>
          <div className="histroy__wrap">
            <div className="top">
              <p className="normal__title f__medium">최근 사용이력</p>
              <button className="custom__btn">
                <span className="f__medium">더보기</span>
              </button>
            </div>
            <div className="common__grid">
              <table>
                <thead>
                  <tr>
                    <th>
                      <p className="f__medium">발생일시</p>
                    </th>
                    <th>
                      <p className="f__medium">사용자명</p>
                    </th>
                    <th>
                      <p className="f__medium">Screen</p>
                    </th>
                    <th>
                      <p className="f__medium">Action</p>
                    </th>
                  </tr>
                </thead>
                <tbody>
                  {history.map((obj, idx) => (
                    <tr key={idx}>
                      <th>
                        <p className="f__medium">{obj.eventTime}</p>
                      </th>
                      <th>
                        <p className="f__medium">{obj.name}</p>
                      </th>
                      <th>
                        <p className="f__medium">{obj.screen}</p>
                      </th>
                      <th>
                        <p className="f__medium">{obj.action}</p>
                      </th>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </section>
      {/* <!-- // 충전소 현황 --> */}
      {/* 대여건수 이력 */}
      <section className="rental__count__history">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">대여 이력</p>
            <select className="custom__select">
              {['7일', '30일', '60일', '90일'].map((obj, idx) => (
                <option key={obj} value={obj}>
                  {obj}
                </option>
              ))}
            </select>
          </div>
          <ReactApexChart
            options={chartOption2()}
            series={[
              {
                name: '대여 실패 건수',
                data: [44, 55, 41, 67, 22, 43],
              },
              {
                name: '대여 성공 건수',
                data: [13, 23, 20, 8, 13, 27],
              },
            ]}
            type="area"
            height="400px"
          />
        </div>
      </section>
      {/* // 대여건수 이력 */}
      {/* 장애 스테이션 건수 */}
      <section className="count__rental__stations">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">장애 이력</p>
            <select className="custom__select">
              {['7일', '30일', '60일', '90일'].map((obj, idx) => (
                <option key={obj} value={obj}>
                  {obj}
                </option>
              ))}
            </select>
          </div>
          <ReactApexChart
            options={chartOption2(['#E65A5A', '#37E60D'])}
            series={[
              {
                name: '장애 스테이션 수',
                data: [20, 60, 35, 22, 70, 30],
              },
              {
                name: '정상 스테이션 수',
                data: [43, 13, 50, 18, 23, 37],
              },
            ]}
            type="area"
            height="400px"
          />
        </div>
      </section>
      {/* // 장애 스테이션 건수 */}
      {/* 그룹별 사용량 */}
      <section className="usage__by__group">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">그룹별 사용량</p>
          </div>
          <div className="chart__wrap">
            <div>
              <ReactApexChart
                options={options('그룹별 스테이션 수의 %')}
                series={[10, 90, 20, 40, 20, 50, 60, 10, 70, 60]}
                type="pie"
                height="300px"
              />
            </div>
            <div>
              <ReactApexChart
                options={options('그룹별 대여성공 건수의 %')}
                series={[50, 40, 30, 10, 0, 30, 20, 10, 90, 60]}
                type="pie"
                height="300px"
              />
            </div>
          </div>
        </div>
      </section>
      {/* // 그룹별 사용량 */}
      {/* <!-- 사용자 인증 건수 --> */}
      <section className="user__auth__count">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">사용자 인증 건수</p>
          </div>
          <div className="wrap">
            <ReactApexChart options={chartData} series={chartData.series} type="bar" />
            <div className="flex__box">
              <div className="item today">
                <p className="f__medium">214</p>
                <p>오늘 사용자 인증 건수</p>
              </div>
              <div className="item suc">
                <p className="f__medium">3</p>
                <p>대여 성공</p>
              </div>
              <div className="item fail">
                <p className="f__medium">4</p>
                <p>대여 실패</p>
              </div>
            </div>
          </div>
        </div>
      </section>
      {/* <!-- // 사용자 인증 건수 --> */}
      {/* <!-- 이용률 높은 스테이션 Top 10 --> */}
      <section className="top10__list station">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">이용률 높은 스테이션 Top 10</p>
            <select className="custom__select" onChange={(e) => setUseStation(e.target.value)}>
              {['주간', '월간', '년간'].map((obj, idx) => (
                <option key={obj} value={obj}>
                  {obj}
                </option>
              ))}
            </select>
          </div>
          <div className="common__grid">
            <table>
              <thead>
                <tr>
                  <th>
                    <p className="f__medium">순위</p>
                  </th>
                  <th>
                    <p className="f__medium">스테이션명</p>
                  </th>
                  <th>
                    <p className="f__medium">디바이스 ID</p>
                  </th>
                  <th>
                    <p className="f__medium">대여건수</p>
                  </th>
                </tr>
              </thead>
              <tbody>
                {top10Station.map((obj) => (
                  <tr key={obj.lank}>
                    <th>
                      <p className="f__medium">{obj.lank}</p>
                    </th>
                    <th>
                      <p className="f__medium">{obj.name}</p>
                    </th>
                    <th>
                      <p className="f__medium">{obj.id}</p>
                    </th>
                    <th>
                      <p className="f__medium">{obj.count}</p>
                    </th>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </section>
      {/* <!-- // 이용률 높은 스테이션 Top 10 --> */}
      {/* <!-- 이용률 높은 사용자 Top 10 --> */}
      <section className="top10__list user">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">이용률 높은 사용자 Top 10</p>
            <select className="custom__select" onChange={(e) => setUser(e.target.value)}>
              {['주간', '월간', '년간'].map((obj, idx) => (
                <option key={obj} value={obj}>
                  {obj}
                </option>
              ))}
            </select>
          </div>
          <div className="common__grid">
            <table>
              <thead>
                <tr>
                  <th>
                    <p className="f__medium">순위</p>
                  </th>
                  <th>
                    <p className="f__medium">이름</p>
                  </th>
                  <th>
                    <p className="f__medium">대여건수</p>
                  </th>
                </tr>
              </thead>
              <tbody>
                {top10User.map((obj) => (
                  <tr key={obj.lank}>
                    <th>
                      <p className="f__medium">{obj.lank}</p>
                    </th>
                    <th>
                      <p className="f__medium">{obj.name}</p>
                    </th>
                    <th>
                      <p className="f__medium">{obj.count}</p>
                    </th>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </section>
      {/* <!-- // 이용률 높은 사용자 Top 10 --> */}
    </div>
  );
}

export default DashBoard;
